body {
  background: url("../img/header-bg.png") no-repeat top right;
  @include sm {
    background: none;
  }
}

.header {
  padding-bottom: 120px;

  @include md {
    padding-bottom: 0;
  }

  &__container {
    margin-bottom: 170px;

    @include sm-min {
      margin-bottom: 80px;
    }
  }

  &__nav {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 46px;
    max-width: 1175px;
    margin: 0 auto;
    position: relative;

    @include md {
      flex-direction: column;
      height: unset;
    }
  }

  &__logo {
    @include md {
      margin-bottom: 20px;
    }
  }

  &__links {
    display: flex;

    @include md {
      margin-bottom: 10px;
    }

    @include sm {
      // display: none;
      flex-direction: column;
      width: 100%;
      position: absolute;
      top: 105%;
      background-color: #fff;
      opacity: 0;
      visibility: hidden;
      transition: opacity 0.5s, visibility 0.5s;
    }

    &.open {
      opacity: 1;
      visibility: visible;
      transition: opacity 0.5s, visibility 0.5s;
      z-index: 100;
    }
  }

  &__search {
    width: 46px;
    height: 46px;
    border-radius: 50%;
    background-color: #fff;
    position: relative;
    transition: box-shadow 0.3s, background-color 0.3s;

    @include sm {
      display: none;
    }

    &:hover {
      box-shadow: 0px 3px 15px rgba(127, 184, 0, 0.2);
      background-color: #7fb800;
      transition: box-shadow 0.3s, background-color 0.3s;
    }

    &::after {
      content: "";
      background: url("../img/search-btn.svg") no-repeat center / cover;
      position: absolute;
      top: 50%;
      left: 50%;
      width: 22px;
      height: 23px;
      transform: translate(-50%, -50%);
    }
  }

  &__content {
    margin-top: 86px;
    display: flex;

    @include md-min {
      margin-top: 40px;
    }

    @include sm {
      margin-top: 10px;
      flex-direction: column;
    }

    @include xs {
      margin-top: 0;
    }
  }

  &__cards {
    display: flex;
    transform: translateY(-65px);

    @include md {
      transform: translateY(0);
    }

    @include sm {
      flex-wrap: wrap;
    }
  }
}

.burger-menu {
  width: 55px;
  padding: 10px;
  border-radius: 5px;
  transition: background-color 0.3s;
  display: none;

  &:hover {
    background-color: $light-green;
    transition: background-color 0.3s;
    span {
      background-color: #fff;
      transition: background-color 0.3s;
    }
  }

  span {
    display: block;
    width: 100%;
    height: 4px;
    background-color: #adb5bd;
    border-radius: 10px;
    transition: background-color 0.3s;

    &:not(:last-child) {
      margin-bottom: 7px;
    }
  }

  @include sm {
    display: block;
  }
}

.nav-fixed {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  padding: 40px 15px 30px 15px;
  background-color: rgba(255, 255, 255, 0.93);
  z-index: 100;

  @include md {
    position: static;
    padding: 30px 15px 15px 15px;
  }

  @include md {
    padding: 20px 0;
  }
}

.header-links {
  &__item {
    &:not(:last-child) {
      margin-right: 40px;

      @include sm {
        margin-right: 0;
      }
    }

    @include sm {
      width: 100%;
      display: flex;
      flex-direction: column;
      align-content: center;
    }

    &--tab {
      position: relative;

      @include sm {
        ul {
          position: static;
          max-height: 0;
          transition: all 0.3s;
          max-width: unset;
          width: 100%;
          li {
            width: 100%;
            display: flex;
            justify-content: center;
            a {
              text-align: center;
            }
          }
        }
        &.open {
          ul {
            display: block;
            max-height: 100vh;
            transition: all 0.3s;
            visibility: visible;
            opacity: 1;
          }
        }
      }

      @media (min-width: 860px) {
        &:hover {
          ul {
            transition: opacity 0.3s;
            visibility: visible;
            opacity: 1;
          }
        }
      }
    }
  }

  &__link {
    font-family: "Rubik", sans-serif;
    font-weight: 400;
    font-size: 18px;
    line-height: 21px;
    transition: transform 0.3s, color 0.3s;
    padding: 10px 20px;
    display: flex;
    align-items: center;

    @include sm {
      // width: 100%;
      display: inilie-flex;
      justify-content: center;
      text-align: center;
      transition: background-color 0.3s, color 0.3s;

      &:hover {
        transition: background-color 0.3s, color 0.3s;
        color: #fff;
        background-color: $light-green;

        svg {
          fill: #fff;
        }
      }
    }

    &--current {
      color: $light-green;
    }

    svg {
      margin-left: 8px;
      width: 10px;
      height: 16px;
      transform: fill 0.3s;
    }

    @media (min-width: 860px) {
      &:hover {
        transition: transform 0.3s, color 0.3s;
        color: $light-green;
        transform: scale(1.1);

        svg {
          transition: fill 0.3s;
          fill: $light-green;
        }
      }
    }
  }
}

.header-content {
  &__title {
    color: $dark-green;
    font-weight: 700;
    font-size: 82px;
    line-height: 98px;
    font-family: "Rubik", sans-serif;
    max-width: 500px;
    margin: 0;
    margin-bottom: 30px;

    @include md {
      font-size: 62px;
      line-height: 72px;
    }

    @include sm {
      max-width: unset;
    }

    @include xs {
      font-size: 42px;
      line-height: 52px;
    }

    span {
      color: $light-green;
    }
  }

  &__text {
    max-width: 670px;
    margin-bottom: 50px;

    @include md {
      max-width: 570px;
    }

    @include sm {
      max-width: unset;
    }
  }

  &__btns {
    display: flex;
    margin-bottom: 80px;

    @include sm {
      flex-direction: column;
      max-width: 400px;
      margin: 0 auto;
      margin-bottom: 80px;
    }

    @include xs {
      margin-bottom: 40px;
    }
  }

  &__about {
    display: inline-block;
    margin-right: 40px;

    @include sm {
      margin-right: 0;
      margin-bottom: 30px;
    }
  }

  &__watch {
    padding-left: 70px;
    background-image: url("../img/play-btn.svg");
    background-repeat: no-repeat;
    background-position: left;

    @include md-min {
      padding: 10px 0;
      padding-left: 70px;
    }

    @include sm {
      padding: 0;
      padding-left: 0;
      padding: 70px 20px 20px 20px;
      background-position: top center;
    }
  }

  &__info {
    margin-top: 30px;

    @include sm {
      order: 1;
      text-align: center;
    }
  }

  &__img {
    object-fit: cover;
    height: 560px;

    @include md {
      height: 460px;
    }

    @include sm {
      object-fit: contain;
      height: 360px;
      order: 0;
    }
  }
}

.header-card {
  max-width: 280px;
  background-color: #ffffff;
  box-shadow: 0px 0px 15px rgba(127, 184, 0, 0.15);
  border-radius: 10px;
  padding: 20px 30px 20px 85px;
  background-repeat: no-repeat;
  background-position: 15px center;

  @include sm {
    text-align: center;
    max-width: unset;
    width: 100%;
    background-position: top 20px center;
    padding: 100px 30px 30px 30px;
  }

  &--first {
    background-image: url("../img/header-card-1.svg");
  }

  &--second {
    background-image: url("../img/header-card-2.svg");
  }

  &--third {
    background-image: url("../img/header-card-3.svg");
  }

  &:not(:last-child) {
    margin-right: 20px;

    @include sm {
      margin-right: 0;
      margin-bottom: 20px;
    }
  }

  &__title {
    font-family: "Rubik", sans-serif;
    font-weight: 500;
    font-size: 20px;
    line-height: 26px;
    color: $dark-green;

    @include sm {
      margin-bottom: 20px;
    }
  }

  &__descr {
    max-width: 161px;
    @include sm {
      max-width: unset;
    }
  }
}

.links-list {
  max-width: 160px;
  background-color: rgba(255, 255, 255, 0.93);

  &--about {
    position: absolute;
    top: 100%;
    left: 0;
    visibility: hidden;
    opacity: 0;
    transition: opacity 0.3s;
  }

  &--blog {
    visibility: hidden;
    opacity: 0;
    position: absolute;
    top: 100%;
    left: 0;
    transition: opacity 0.3s;
  }

  &__item {
    background-color: #fff;
    transition: background-color 0.3s;
    padding: 10px 20px;
    display: block;
    &:hover {
      background-color: $light-green;
      transition: background-color 0.3s;

      .links-list__link {
        color: #fff;
      }
    }
  }

  &__link {
    font-family: "Rubik", sans-serif;
    font-weight: 400;
    font-size: 18px;
    line-height: 21px;
    transition: transform 0.3s, color 0.3s;
    display: inline-block;
  }
}
