.news-main {
  margin-bottom: 290px;

  @include md {
    margin-bottom: 170px;
  }

  @include sm-min {
    margin-bottom: 80px;
  }

  &__container {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-template-areas: "news news filters";

    @include sm {
      grid-template-areas:
        "news news news"
        "filters filters filters";
    }
  }

  &__news {
    grid-area: news;

    @include sm {
      margin-bottom: 50px;
    }
  }

  &__filters {
    grid-area: filters;
    margin-left: 40px;

    @include sm {
      margin-left: 0;
      max-width: 600px;
      margin: 0 auto;
    }
  }
}

.news-new {
  height: 265px;
  display: flex;

  @include sm {
    flex-direction: column;
    height: unset;
  }

  &:not(:last-child) {
    margin-bottom: 45px;
  }

  &__img {
    display: block;
    width: 100%;
    height: 265px;
    object-fit: cover;
    margin-right: 20px;

    @include md {
      width: 300px;
    }

    @include sm {
      width: unset;
      margin-right: 0;
    }
  }

  &__info {
    max-width: 380px;
    padding: 20px 0;
    background-color: #fff;
  }

  &__date {
    font-weight: 400;
    color: $light-green;
    font-style: italic;
    margin-right: 20px;
    margin-bottom: 15px;
  }

  &__author {
    color: #888888;
    font-style: italic;
  }

  &__title {
    font-family: "Rubik", sans-serif;
    font-weight: 500;
    font-size: 24px;
    line-height: 30px;
    color: $dark-green;
    margin-bottom: 20px;
  }

  &__text {
    margin-bottom: 25px;
  }

  &__link {
    display: inline-block;
    font-family: "Rubik", sans-serif;
    font-weight: 400;
    font-size: 18px;
    line-height: 28px;
    padding-right: 20px;
    background-image: url("../img/arrow-right.svg");
    background-repeat: no-repeat;
    background-position: right;
  }
}

.news-filters {
  &__search {
    margin-bottom: 35px;
  }
}

.filters-title {
  font-family: "Rubik", sans-serif;
  font-weight: 500;
  font-size: 20px;
  line-height: 26px;
  color: $dark-green;
  padding-left: 15px;
  position: relative;
  margin-bottom: 20px;

  &::before {
    content: "";
    width: 6px;
    height: 6px;
    border-radius: 50%;
    position: absolute;
    left: 0;
    top: 50%;
    transform: translateY(-50%);
    background-color: #7fb800;
  }
}

.news-categories {
  padding: 40px 30px;
  box-shadow: 0px 0px 15px rgba(127, 184, 0, 0.15);
  border-radius: 30px;
  margin-bottom: 30px;

  &__category {
    &:not(:last-child) {
      margin-bottom: 5px;
    }
  }

  &__link {
    font-weight: 400;
    transition: color 0.3s;

    &:hover {
      color: $light-green;
      transition: color 0.3s;
    }
  }
}

.news-latests {
  padding: 40px 30px;
  box-shadow: 0px 0px 15px rgba(127, 184, 0, 0.15);
  border-radius: 30px;
  margin-bottom: 35px;

  &__new {
    &:not(:last-child) {
      margin-bottom: 15px;
    }
  }
}

.latests-link {
  color: #adb5bd;
  font-style: italic;

  &__title {
    font-style: normal;
    font-family: "Rubik", sans-serif;
    font-weight: 400;
    font-size: 18px;
    line-height: 28px;
    color: $dark-green;
    margin-bottom: 5px;
  }

  &__date {
    margin-right: 20px;
  }
}

.news-tags {
  padding: 40px 30px;
  box-shadow: 0px 0px 15px rgba(127, 184, 0, 0.15);
  border-radius: 30px;
  margin-bottom: 35px;
  display: flex;
  flex-wrap: wrap;

  &__title {
    width: 100%;
  }

  &__tag {
    margin-right: 10px;
    margin-bottom: 10px;

    &:nth-last-child(-n + 3) {
      margin-bottom: 0;
    }

    @include xs {
      &:not(:last-child) {
        margin-bottom: 10px;
      }
    }

    // &:nth-child(3n) {
    //   margin-right: 0;
    // }
  }

  &__link {
    display: inline-block;
    padding: 5px 10px;
    line-height: 1;
    border: 1px solid #adb5bd;
    border-radius: 15.5px;
  }
}

.news-search {
  &__input {
    border: 1px solid #adb5bd;
    border-radius: 30px;
    padding: 15px 30px;
    width: 100%;
    margin-bottom: 20px;
    &::-webkit-search-decoration,
    &::-webkit-search-cancel-button,
    &::-webkit-search-results-button,
    &::-webkit-search-results-decoration {
      display: none;
    }

    &::placeholder {
      font-family: Lato;
      font-style: italic;
      font-weight: 400;
      font-size: 17px;
      line-height: 26px;
      color: #adb5bd;
    }
  }

  &__btn {
    display: block;
    padding: 15px 30px;
    background-color: $light-green;
    border-radius: 30px;
    width: 100%;
    color: #fff;

    &:hover {
      transition: background-color 0.3s;
      background-color: $dark-green;
    }
  }
}

.news-banner {
  min-height: 380px;
  background: url("../img/news-banner.png") no-repeat center / cover;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  position: relative;

  &::after {
    content: "";
    width: 100%;
    height: 100%;
    background-color: rgba(#fff, 0.4);
    position: absolute;
    left: 0;
    top: 0;
  }

  &__title {
    font-family: "Rubik", sans-serif;
    font-weight: 500;
    font-size: 28px;
    line-height: 36px;
    color: #1d4d4f;
    position: relative;
    z-index: 10;
  }

  &__link {
    position: relative;
    z-index: 10;
  }
}

.news-pagination {
  margin-top: 70px;
  display: flex;
  justify-content: center;
  grid-column-start: 1;
  grid-column-end: 4;

  &__item {
    &:not(:last-child) {
      margin-right: 40px;
    }
  }

  &__link {
    &--current {
      color: $light-green;
    }
  }
}
