.footer {
  background-image: url("../img/footer-bg.png");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  min-height: 606px;

  @include md {
    min-height: unset;
  }

  &__inner {
    display: flex;
    position: relative;

    @include sm {
      flex-direction: column;
    }
  }

  &__container {
    padding-top: 120px;

    @include md {
      padding-top: 20px;
    }

    @include sm {
      padding-top: 0;
    }
  }

  &__logo {
    display: block;
    margin-bottom: 20px;

    img {
      object-fit: cover;
    }
  }

  &__info {
    margin-right: 120px;

    @include md {
      padding-top: 80px;
      margin-right: 40px;
    }

    @include sm {
      padding-top: 0;
      margin-right: 0;
      margin-bottom: 40px;
    }
  }

  &__text {
    max-width: 500px;
    margin-bottom: 40px;
  }

  &__copyright {
    span {
      font-weight: 700;
    }
  }
}

.socials-list {
  display: flex;
  margin-bottom: 80px;

  @include sm {
    margin-bottom: 40px;
  }

  &__item {
    &:not(:last-child) {
      margin-right: 40px;
    }
  }

  &__link {
    svg {
      transition: transform 0.3s;
    }

    &:hover {
      svg {
        transition: transform 0.3s;
        transform: scale(1.2);
      }
    }
  }
}

.footer-newsletter {
  background: linear-gradient(334.24deg, #7fb800 -9.84%, #0f9b0f 121.28%);
  border-radius: 50px 50px 0px 0px;
  text-align: center;
  padding: 172px 67px;
  position: absolute;
  right: 0;
  top: -61%;

  @include md {
    position: static;
    padding: 80px 40px;
  }

  @include sm {
    padding: 40px;
    max-width: 400px;
    margin: 0 auto;
  }

  @include xs {
    padding: 20px 10px;
  }

  &__title {
    font-family: "Rubik", sans-serif;
    font-weight: 700;
    font-size: 42px;
    line-height: 56px;
    color: #ffffff;
    max-width: 440px;
    margin: 0 auto;
    margin-bottom: 20px;

    span {
      font-weight: 400;
    }

    @include xs {
      font-size: 32px;
      line-height: 42px;
    }
  }

  &__text {
    max-width: 460px;
    color: #e2f6b9;
    font-weight: 400;
    margin-bottom: 40px;

    @include sm {
      max-width: unset;
    }
  }
}

.newsletter-form {
  &__input {
    border: 1px solid #e2f6b9;
    border-radius: 30px;
    background-color: transparent;
    padding: 15px 30px;
    display: block;
    margin: 0 auto;
    margin-bottom: 40px;

    &::placeholder {
      font-family: "Lato", sans-serif;
      font-weight: 400;
      font-size: 17px;
      line-height: 26px;
      color: #ffffff;
    }
  }

  &__btn {
    background: $orange;

    &:hover {
      &:hover {
        background-color: #caa43b;
        transition: box-shadow 0.3s, background-color 0.3s;
        box-shadow: 0px 4px 15px rgba(127, 184, 0, 0.401852);
      }
    }
  }
}
