.news-hero {
  min-height: 430px;
  background: url("../img/news-hero.png") no-repeat center / cover;
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding-left: 260px;
  margin-bottom: 170px;
  position: relative;

  @include md-min {
    padding-left: 60px;
  }

  @include sm {
    min-height: 330px;
    margin-bottom: 80px;
  }

  @include xs {
    min-height: 200px;
    padding-left: 15px;
  }

  &::after {
    content: "";
    width: 100%;
    height: 100%;
    position: absolute;
    left: 0;
    top: 0;
    background-color: rgba(#fff, 0.2);
  }

  &__title {
    font-family: "Rubik", sans-serif;
    font-weight: 400;
    font-size: 82px;
    line-height: 98px;
    color: $dark-green;
    margin-bottom: 10px;
    position: relative;
    z-index: 10;

    @include sm-min {
      font-size: 62px;
      line-height: 72px;
    }
  }

  &__breadcrumbs {
    position: relative;
    z-index: 10;
  }

  &__link {
    font-weight: 400;

    &:not(:last-child) {
      &::after {
        content: "/";
        margin-left: 5px;
      }
    }
  }
}
